<template>
  <div class="homepage">
    <div class="main">
      <div class="top">
        <img src="../assets/01_01.png" alt="" class="bg">
      </div>
      <div class="bottom">
        <img src="../assets/01_02.jpg" alt="" class="bg">
        <div class="video video-box" @click="handleClick" v-tap="{methods:handleClick}">
          <video id="video" poster="../assets/main.png" class="mp4">
            <source src="http://ugcdl.video.gtimg.com/g0682s414cu.mp4?sdtfrom=v1010&guid=c1145a06a12404dc4299bc0bbd81fd37&vkey=24799572291E4950E680237759CB5D94272552096D85738D238F920EE330683F3CDB03C13099E869DFF43D3F0C1A7DBC1246CD8D40C745AA05541AF806471BDBCA1E0BF2D1FA771AC0C9BCD6878DEBF4B42940A59B042C6C504B8B9472473059EF569FBFA6988986831779B426C54D98163C101304BA9843" type="video/mp4" />
          </video>
          <icon name="play" scale="2" v-if="isPause" class="icon"></icon>
        </div>
        <router-link class="down-arrow" to="/main">
          <icon name="arrow-down" scale="2"  style="color: #fff"></icon>
          <h3>活动详情</h3>  
        </router-link>
      </div>
    </div>
    <div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      isPause: true
    }
  },
  watch: {
    isPause: function (val) {
      console.log(val)
    }
  },
  mounted() {
    this.changePaused()
  },
  methods: {
    handleClick () {
      let player = document.getElementById('video');
      
      if (!this.isPause) {
        this.isPause = true
        player.pause()
      } else {
        this.isPause = false
        player.play()
      }
    },
    changePaused () {
      let player = document.getElementById('video');
      let self = this
      let timer = setInterval(() => {
        this.isPause = player.paused;
         player.addEventListener("ended",function(){
          clearInterval(timer);
          self.$router.push('/main')
        })
      }, 1000)
    }
  }
}
</script>

<style scoped>
a {
  text-decoration: none
}
.video {
  width: 68%;
  top: 8px;
  position: absolute;
  height: 38%;
}
.homepage {
  width: 100%;
  min-height: 100%;
  z-index: 1;
}
.main {
  width: 100%;
  min-height: 100%;
}
.pic {
  width: 70%;
  height: 204px;
  margin-top: 37%;
}
.bg {
  width: 100%;
  vertical-align: middle;
}
.image {
  width: 100%;
  vertical-align: middle;
}
.bottom {
  position: relative;
  align-items: center;
  justify-content: center;
  display: flex;
}
.video-box {
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
}
.mp4 {
  width: 100%;
  height: 100%;
}
.icon {
  position: absolute;
  color: #ffffff;
}
.down-arrow {
  position: absolute;
  bottom: 5px;
}
.down-arrow h3 {
  margin: 5px 0;
  color: #ffffff;
}
</style>



