<template>
  <div class="homepage">
    <img src="../assets/pic.jpg" alt="" srcset="" class="image">
  </div>
</template>

<script>
export default {
  name: 'MainPage',
  data () {
    return {
      isPause: true
    }
  }
}
</script>

<style scoped>
a {
  color: #42b983;
}
.video {
  width: 70%;
  top: 3px;
  left: 15%;
  position: absolute;
  height: 40%;
}
.homepage {
  width: 100%;
  min-height: 100%;
  z-index: 1;
}
.main {
  width: 100%;
  min-height: 100%;
}
.pic {
  width: 70%;
  height: 204px;
  margin-top: 37%;
}
.bg {
  width: 100%;
  vertical-align: middle;
}
.image {
  width: 100%;
  vertical-align: middle;
}
.bottom {
  position: relative;
}
.video-box {
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
}
.mp4 {
  width: 100%;
  height: 100%;
}
.icon {
  position: absolute;
  color: #ffffff;
}
</style>



